.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.ant-table-small > .ant-table-content > .ant-table-body {
   margin: 0 !important; 
}

.ant-form-item{
  margin-bottom: 0 !important; 
}

.custom-cursor-pointer:hover{
  cursor: pointer;
}


.form-custom-item-footer{
  padding-top: 20px;
}

.form-custom-item{
  padding: 0 13px !important;
}